export default {
	data() {
		return {
			form: {
				back_ad: "", //所属银行
				back_name: "", //开户人
				back_id: "" //银行卡号
			}
		}
	},
	onLoad() {
		this.getWalletInfo();
	},
	methods: {
		getWalletInfo() {
			this.api({
				method: "post",
				url: "User/index"
			}).then(res=> {
				console.log(res);
				this.form.back_ad = res.data.date.back_ad;
				this.form.back_name = res.data.date.back_name;
				this.form.back_id = res.data.date.back_id;
			})
		},
		bind() {
			console.log(this.form);
			if(this.form.back_ad == "") {
				uni.showToast({
					title: "请输入所属银行信息",
					duration: 1000,
					icon: 'none'
				})
			} else if(this.form.back_name == "") {
				uni.showToast({
					title: "请输入银行卡开户人姓名",
					duration: 1000,
					icon: 'none'
				})
			} else if(this.form.back_id == "") {
				uni.showToast({
					title: "请输入银行卡卡号",
					duration: 1000,
					icon: 'none'
				})
			} else {
				this.api({
					method: "POST",
					url: "User/tixian_edit",
					data: this.form
				}).then(res=> {
					console.log(res);
					if(res.data.code == 1 && res.data.msg == '修改成功') {
						uni.showModal({
							title: '提示',
							content: '账号添加/修改成功',
							showCancel: false,
							cancelText: '',
							confirmText: '确定',
							success: res => {
								if(res.confirm) {
									uni.switchTab({
										url: '../../userTab/mine'
									})
								}
							},
							fail: () => {},
							complete: () => {}
						});
					} else if (res.data.code == 2){
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon: 'none'
						})
					}
				})
			}
		}
	}
}
